/* variable*/
.classname {
  color: #C81013;
}

body {
  margin: 0px;
  font-family: Roboto Condensed, serif;
  font-size: 18px;
  line-height: 1.5;
  color: #373a3c;
}

* {
  box-sizing: border-box;
}

.container-header {
  /* contain of slide at top*/
  width: 100%;
  height: 600px;
  position: relative;
  top: 0;
  left: 0;
}

.slide-contain {
  display: block;
  transition: 2s ease-in opacity;
  opacity: 1;
}

/* Next & previous buttons */
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 16px;
  margin-top: -22px;
  margin-left: 20px;
  opacity: 0;
  color: white;
  font-weight: bold;
  font-size: 32px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
}

.container-header:hover .next, .container-header:hover .prev {
  opacity: 1;
}

/* Position the "next button" to the right */
.next {
  right: 20px;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  text-decoration: none;
}

.dot {
  height: 4px;
  width: 35px;
  position: relative;
  bottom: 80px;
  right: 2%;
  /* it work with div style that conatain this dot */
  background-color: rgba(243, 240, 240, 0.5);
  display: inline-block;
  transition: background-color 0.6s ease;
  cursor: pointer;
}

/* On smaller screens, decrease text size */
@media only screen and (max-width: 300px) {
  .prev,
  .next {
    font-size: 11px;
  }
}

.make-background,
.dot:hover {
  background-color: white;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -moz-animation-name: fade;
  -ms-animation-name: fade;
  animation-name: fade;
  -webkit-animation-duration: 20s;
  -moz-animation-duration: 20s;
  -ms-animation-duration: 20s;
  animation-duration: 20s;
}

@-webkit-keyframes fade {
  0% {
    opacity: 0.6;
  }
  17% {
    opacity: 1;
  }
  25% {
    opacity: 1;
  }
  80% {
    opacity: 0.9;
  }
  92% {
    opacity: 0.7;
  }
}

@keyframes fade {
  0% {
    opacity: 0.2;
  }
  17% {
    opacity: 1;
  }
  25% {
    opacity: 1;
  }
  92% {
    opacity: 0.2;
  }
}

/* On smaller screens, decrease text size */
@media only screen and (max-width: 300px) {
  .prev,
  .next {
    font-size: 11px;
  }
}

.background-img {
  /* slide image background  of top */
  width: 100%;
  height: 600px;
}

.logo {
  /* div for logo side*/
  width: 30%;
  height: 91px;
  position: absolute;
  top: 0;
  left: 0;
  padding-left: 20px;
}

.logo img {
  width: auto;
  height: 51px;
  margin-bottom: 0px;
  margin-top: 10px;
  margin-left: 50px;
}

.logo p {
  font-size: 14px;
  color: #3294FC;
  margin-top: 0px;
  margin-left: 50px;
}

.navigation-bar {
  width: 100%;
  height: 91px;
  position: fixed;
  top: 0;
  left: 0;
  background-color: white;
  border-bottom: none;
}

/* Add background=none to navigation of about section logo in about div*/
.background-none {
  background: transparent;
}

.navbar-nav {
  height: 91px;
  background-color: #ffcb08;
  padding-right: 20px;
  margin-right: -25px;
}

.navigation-list li {
  margin-top: 20px;
}

.navigation-list li a.focus::after {
  /* make underline when mouse focus on nav list*/
  content: "";
  display: block;
  height: 2px;
  background-color: #fff;
  width: 25px;
  position: relative;
  margin: auto;
}

.navigation-list li:nth-child(1) {
  width: 0px;
}

.navbar-nav li a {
  color: white !important;
}

#nav-hide:after {
  content: "";
  position: absolute;
  top: 0;
  right: 100%;
  border-width: 48px;
  border-style: solid;
  border-color: transparent #ffcb08 #ffcb08 transparent;
  margin-top: -25px;
}

.navigation-shadow {
  box-shadow: #000 2px 0px 4px;
  position: fixed;
}

/* style for make navigation list*/
.navbar-inverse .navbar-toggle {
  border: none;
}

.navbar-inverse .navbar-toggle .icon-bar {
  background-color: black;
}

.navbar-inverse .navbar-toggle:focus, .navbar-inverse .navbar-toggle:hover {
  background-color: transparent;
}

.navbar-inverse .navbar-collapse, .navbar-inverse .navbar-form {
  border: none;
}

/* style for Introduction in center of header*/
.intro-in-header {
  width: 70%;
  height: 100px;
  margin: auto;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.54);
  position: absolute;
  top: 250px;
  left: 15%;
  text-align: center;
  font-size: 20px;
}

.intro-in-header p {
  color: white;
  text-indent: 20px;
}

/* style for button in header*/
.button-head {
  position: absolute;
  top: 400px;
  left: 43%;
  background-color: #ffcb08;
  padding: 10px;
  border: none;
  font-weight: bold;
}

.button-head:hover {
  opacity: 0.7;
  color: white;
}

/* about section */
.container-about {
  background-color: rgba(188, 184, 184, 0.15);
}

.about-text {
  /* div contain only text of about */
  margin-top: 40px;
  margin-bottom: 40px;
}

/* title*/
