  height: 50px;
  margin-bottom: 0px;
  margin-top: 10px;
  margin-left: 50px;
}

.about-logo p {
  font-size: 14px;
  color: #3294FC;
  margin-top: 0px;
  margin-left: 50px;
}

.product-title {
  width: 100%;
  height: 80px;
  text-align: center;
}

.product-title h3 {
  font-size: 24px;
  color: #ffcb08;
  padding: 5px;
  border-bottom: #ffcb08 1px groove;
  display: inline-block;
}

.product-img {
  width: 95%;
  /*height: 300px;*/
}

h4.caption {
  color: #16AC92;
  margin: 0px;
  font-size: 24px;
  margin-bottom: 40px;
}

.line-btm {
  width: 10%;
  text-align: center;
  border: 1px solid #ffcb08;
}

/* for add float to image  in product section*/
.float-right {
  float: right;
}

.float-left {
  float: left;
}

.padding-btm {
  padding-bottom: 60px;
}

/* contain blur image  at bottom*/
.contain {
  width: 100%;
  height: 100%;
  background-image: url("Picture/web-comp/wharehouse.jpg");
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.bottom-img {
  width: 100%;
  height: 350px;
  margin-top: 10px;
  position: relative;
  /* The image used */
}

.more-on-intro {
  top: 100px;
  background-color: transparent;
  left: 15%;
}

.more-on-btn {
  /* it take style more on style of button in header*/
  top: 200px;
}

/* follow us div*/
.follow-us-contain {
  width: 100%;
  height: 300px;
  color: #ffcb08;
  text-align: center;
  background-color: rgba(188, 184, 184, 0.15);
}

/* style line (hr tag) in Bottom*/
hr.line-col-fade-left-right {
  border: none;
  height: 1px;
  background-image: linear-gradient(to right, transparent, #e8e8e8, transparent);
  max-width: 400px;
}

hr.marg-md {
  margin: 3rem auto;
}

.contain-social-media {
  /* contain social media button*/
  width: 50%;
  height: 100px;
  margin-left: 25%;
  margin-top: 60px;
}

/* style on social media image */
