}

/* style for make at bottom*/
.at-bottom {
  position: absolute;
  background-color: #ffcb08;
  text-align: center;
  color: #373a3c;
  width: 100%;
  height: 200px;
  padding-top: 10px;
}

.at-bottom ul {
  font-weight: bold;
  list-style: none;
}

.at-bottom li {
  display: inline-block;
  padding-left: 40px;
}

.at-bottom li a {
  text-decoration: none;
  color: #373a3c;
}

.at-bottom li a:hover {
  color: white;
}

.at-bottom P {
  margin-top: 70px;
}

.at-bottom P a {
  color: white;
  cursor: pointer;
}

